.section {
  padding-top: 100px;
  padding-bottom: 100px;
  &:nth-child(odd) {
    background-color: $white-dark; }
  &__title, &__subttl {
    text-align: center; }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__title-mb-lg {
    margin-bottom: 50px;
    @include xl {
      margin-bottom: 40px; }
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__subttl {
    margin-bottom: 45px;
    max-width: 580px;
    margin-left: auto;
    margin-right: auto;
    @include lg {
      margin-bottom: 35px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; } }
  &__block {
    margin-bottom: 80px;
    @include xl {
      margin-bottom: 60px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } } }
