.project-link {
  @include flex(flex-end, normal, column);

  color: $white;
  height: 100%;
  padding: 50px 35px;
  padding-top: 125%;
  @include cover;
  @include xl {
    padding: 40px 35px;
    padding-top: 125%; }
  @include lg {
    padding: 35px;
    padding-top: 125%; }
  @include md {
    padding: 25px;
    padding-top: 125%; }
  @include sm {
    padding: 15px;
    padding-top: 125%; }
  &__title {
    margin-bottom: 10px;
    display: block;
    @include md {
      margin-bottom: 5px; } }
  &__text {
    margin-bottom: 25px;
    display: block;
    @include notlast {
      @include md {
        margin-bottom: 15px; } } } }
