.hero {
  padding-top: $header-height;
  position: relative;
  color: $white;
  @include cover;
  @include lg {
    padding-top: $header-height-lg; }
  @include sm {
    padding-top: $header-height-sm; }
  @include xs {
    padding-bottom: 15px; }
  &__content {
    @include flex(center, normal, column);
    padding-top: 25px;
    padding-bottom: 25px;
    @include xs {
      margin-bottom: 15px; }
    html:not(.is-ie) & {
      min-height: calc(100vh - #{$header-height});
      @include xs {
        min-height: 0; } }
    .is-ie & {
      height: calc(100vh - #{$header-height});
      @include xs {
        height: auto; } } }
  &__img {
    @include flex(center, normal, column);
    height: 100%; }
  &__title {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__text {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 25px; } }
  &__btns {
    @include flex();
    @include xl {
      display: block; } }
  &__btn {
    @include notlast {
      margin-right: 15px;
      @include xl {
        margin-right: 0;
        margin-bottom: 15px; } } } }
